<template>
	<div id="app"  class="indeker">
        <div class="web_bg" style=''></div>
        <div class="baster">
            <div class="link">
                <p>隐迹查询服务平台</p>
            </div>
            <div class="foe">
                <div class="nert">
                    <p>用户登录</p>
                    <div class="houser">
                        <img src="~@assets/img/logi.png">
                        <input  type="text" id="name" class="nameer" v-model="name"   placeholder="请输入用户名" autocomplete="off">
                    </div>
                    <div class="houser">
                        <img src="~@assets/img/losuo.png">
                        <input  name='pasuser' type="password" id="name" class="passwoder"  v-model="password"  placeholder="请输入密码" autocomplete="off">
                    </div>
                    <div class="logding">
                        <button  class="batn" @click.stop="login">登录</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {userlogin} from "@/network/admin";
    const sha256 = require("js-sha256").sha256; //引入sha256库
	export default {
		name:'app',
		data() {
            return {
                name: '',
                password: '', 
                
            }
        },
        created() {
           
        },
        mounted() {//组件初始化完成调用方法

        },
        methods: {
        	login(){
        		var name =this.name;
				var upwd = this.password;
				if (name == "") {
		             this.$message.error('用户不能为空！');
		             return false;
		        }
				if (upwd == "") {
		            this.$message.error('密码不能为空！');
                    return false;
		        }
                var data ={
                    userName:this.name,
                    password:sha256(this.password),
                }
                userlogin(data).then(res => {
                    if (res.code== 0) {
                        var permissions = res.data.permissions;
                        var user={'name':this.name};
                        var permissions ={permissions}
                        this.$store.commit('setdataadminuser',user)
                        this.$store.commit('permissions',permissions)
                        layer.msg("登录成功！", {
                            icon: 1,
                            time: 500
                        });
                        this.name= '';
                        this.password = '';
                        if(res.data.permissions.length === 4){
                            this.$router.push({ path: '/Charging/Administration/QueryAdminist'});
                            return
                        }
                        for(let key  in res.data.permissions){
                            var permissions = res.data.permissions[key];
                             if(permissions.permitId == 1){
                                this.$router.push({ path: '/Charging/Administration/QueryAdminist' });
                            }
                            if(permissions.permitId == 2){
                                this.$router.push({ path: '/Charging/Administration/administration' });
                            }
                            if(permissions.permitId == 3){
                                this.$router.push({ path: '/Charging/Administration/Application' });
                            }
                            if(permissions.permitId == 4){
                                this.$router.push({ path: '/Charging/Administration/Addstatistics' });
                            }
                        }

                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });

                    }
                })
				
        	}
        },
	}
</script>

<style>
	@import "~@assets/css/user/Bilinglogin.css";
	/*@import "~@assets/css/login/common.css";*/
	/*@import "~@assets/js/resetMessage.js";*/
	
</style>